.center {
    display: flex;
    justify-content: center;
    align-items: flex-start;
    min-height: 100vh;
    width: 100%;
    overflow: hidden;
    padding: 2rem 0.5rem;
}

.addPlaceFORM
{
    display: flex;
    flex-direction: column;
    background-color: #f8f8f8;
    width: 100%;
    max-width: 700px;
    margin: 1rem auto;
    border-radius: 5px;
    overflow: hidden;
    padding: 1rem 0.5rem;
    color: #6d6868;
}
.addPlaceFORM input , .addPlaceFORM textarea
{
    border-radius: 5px;
    border: none;
    padding: 5px;
    margin-bottom: 1rem;
}
